<template>
  <div class="memberupdate">
    <h2 class="mt-4"></h2>
    <b-container>
      <b-modal v-model="showModalPwd" title=" " @ok="onPasswordConfirm">
        <b-form-input
          v-model="passwordCheck"
          type="password"
          placeholder=" "
        ></b-form-input>
      </b-modal>
      <b-modal v-model="showModalCheck" title="" @ok="moveMemberInfo">
        <p class="my-4"></p>
      </b-modal>
      <b-form @submit="onSubmit">
        <b-form-group id="user_id" label=" :" label-for="user_id">
          <b-form-input
            id="user_id"
            v-model="form.userId"
            type="text"
            placeholder=""
            readonly
          ></b-form-input>
        </b-form-group>

        <b-form-group id="user_name" label=" :" label-for="user_name">
          <b-form-input
            id="user_name"
            type="text"
            v-model="form.userName"
            placeholder=""
          ></b-form-input>
        </b-form-group>

        <b-form-group id="user_name" label=" :" label-for="user_email">
          <b-form-input
            id="user_email"
            type="text"
            v-model="form.email"
            placeholder=""
          ></b-form-input>
        </b-form-group>

        <!-- <b-form-group id="user_password" label=" :" label-for="user_password">
          <b-form-input
            id="user_password"
            type="password"
            v-model="form.userPassword"
            placeholder=""
          ></b-form-input>
        </b-form-group>

        <b-form-group id="password_check" label=" :" label-for="password_check">
          <b-form-input
            id="password_check"
            type="password"
            placeholder=" "
          ></b-form-input>
        </b-form-group> -->

        <b-form-group id="user_joindate" label=" :" label-for="user_joindate">
          <b-form-input
            id="user_joindate"
            type="text"
            v-model="form.joinDate"
            placeholder=" "
            readonly
          ></b-form-input>
        </b-form-group>
        <b-alert :show="showAlertId" :variant="alertVariantId">{{ alertMessageId }}</b-alert>

        <b-button variant="white" class="btn-outline-danger m-2" to="/memberinfo"
          ></b-button
        >
        <b-button type="submit" variant="primary" class="m-2"></b-button>
      </b-form>
    </b-container>
  </div>
</template>
<script>
import { mapState } from "vuex";
import { isCorrectPwd, update } from "@/api/member";

const memberStore = "memberStore";

export default {
  name: "MemberUpdateView",
  data() {
    return {
      form: {
        userId: null,
        userName: null,
        email: null,
        joinDate: null,
      },
      user: {
        userId: null,
        userPassword: null,
      },
      showModalPwd: false,
      showModalCheck: false,
      passwordCheck: "",
      showAlertId: false,
      alertMessageId: "",
      alertVariantId: "danger",
    };
  },
  computed: {
    ...mapState(memberStore, ["userInfo"]),
  },
  mounted() {
    this.form.userId = this.userInfo.userId;
    this.form.userName = this.userInfo.userName;
    this.form.joinDate = this.userInfo.joinDate;

    if (this.userInfo.email != null) {
      this.form.email = this.userInfo.email;
    }
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      this.showModalPwd = true;
      this.passwordCheck = "";
    },
    onReset(event) {
      event.preventDefault();
      // Reset our form values
      this.form.userId = "";
      this.form.userName = "";
      this.form.userPassword = "";
      this.form.joinDate = "";
    },
    onPasswordConfirm() {
      const passwordCheck = this.passwordCheck;
      if (passwordCheck !== "") {
        this.user.userId = this.userInfo.userId;
        this.user.userPassword = passwordCheck;

        //       ->  1 ,  0 
        isCorrectPwd(this.user, ({ data }) => {
          if (data === 1) {
            // 
            update(this.form, ({ data }) => {
              console.log(data);
              // memberinfo 
              this.showModalCheck = true;
            });
          }
          if (data === 0) {
            //  
            this.alertMessageId = "  .";
            this.alertVariantId = "danger";
            this.showAlertId = true;
          }
        });
      }
    },
    moveMemberInfo() {
      this.$router.push({ name: "memberinfo" });
    },
  },
};
</script>
<style></style>
