import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';

//module import
import memberStore from './memberStore';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		articleNo: '',
		MY_PLAN: [],
	},
	getters: {},
	mutations: {
		setArticleNo(state, value) {
			state.articleNo = value;
		},
		addMyPlan(state, item) {
			state.MY_PLAN.push(item); //index 0  item 
		},
		removeMyPlan(state, index) {
			if (index !== -1) {
				state.MY_PLAN.splice(index, 1); // 1  
			}
		},
	},
	actions: {},
	modules: {
		memberStore,
	},
	plugins: [
		createPersistedState({
			//     localStorage  sessionStorage . (default: localStorage)
			storage: sessionStorage,
		}),
	],
});
