<template>
	<div class="memberinfo">
		<h2 class="mt-4"></h2>
		<b-container>
			<b-modal v-model="showModalDel" title="" @ok="onClickDel">
				<p class="my-4"> ? </p>
			</b-modal>
			<b-modal v-model="showModalFin" title="" @ok="moveHome" ok-only>
				<p class="my-4">{{ modalFinMessage }}</p>
			</b-modal>
			<b-form @submit="onSubmit">
				<b-form-group id="user_id" label=" :" label-for="user_id">
					<b-form-input id="user_id" v-model="userInfo.userId" type="text" readonly> </b-form-input>
				</b-form-group>

				<b-form-group id="user_name" label=" :" label-for="user_name">
					<b-form-input
						id="user_name"
						type="text"
						v-model="form.userName"
						placeholder=""
						readonly
					></b-form-input>
				</b-form-group>

				<b-form-group id="user_name" label=" :" label-for="email">
					<b-form-input
						id="email"
						type="text"
						v-model="form.email"
						placeholder=""
						readonly
					></b-form-input>
				</b-form-group>

				<b-form-group id="user_grade" label=" :" label-for="user_grade">
					<b-form-input
						id="user_grade"
						type="text"
						v-model="form.userGrade"
						placeholder=" "
						readonly
					></b-form-input>
				</b-form-group>
				<!-- 
					<b-form-group id="input-group-4" v-slot="{ ariaDescribedby }">
						<b-form-checkbox-group
							v-model="form.checked"
							id="checkboxes-4"
							:aria-describedby="ariaDescribedby"
						>
							<b-form-checkbox value="me">Check me out</b-form-checkbox>
							<b-form-checkbox value="that">Check that out</b-form-checkbox>
						</b-form-checkbox-group>
					</b-form-group> -->

				<b-button type="submit" variant="white" class="btn-outline-danger m-2"></b-button>
				<b-button variant="primary" to="/memberupdate" class="m-2"></b-button>
			</b-form>
		</b-container>
	</div>
</template>
<script>
import { mapState, mapActions } from 'vuex';
import { deleteMember } from '@/api/member';

const memberStore = 'memberStore';

export default {
	name: 'MemberInfoView',
	data() {
		return {
			form: {
				userId: null,
				userName: null,
				userGrade: null,
				email: null,
			},
			showModalDel: false,
			showModalFin: false,
			modalFinMessage: '',
		};
	},
	mounted() {
		this.form.userId = this.userInfo.userId;
		this.form.userName = this.userInfo.userName;

		if (this.userInfo.email === null) {
			this.form.email = ' .';
		}
		if (this.userInfo.email != null) {
			this.form.email = this.userInfo.email;
		}
		if (this.userInfo.userId === 'admin') {
			this.form.userGrade = '';
		}
		if (this.userInfo.userId != 'admin') {
			this.form.userGrade = ' ';
		}
	},
	computed: {
		...mapState(memberStore, ['userInfo']),
	},
	methods: {
		...mapActions(memberStore, ['userLogout']),
		onSubmit(event) {
			event.preventDefault();
			this.showModalDel = true;
		},
		onClickDel() {
			//delflag 
			deleteMember(this.userInfo.userId, ({ data }) => {
				if (data === 'success') {
					// 
					this.modalFinMessage = ' ';
					this.showModalFin = true;
				}
				if (data === 'fail') {
					this.modalFinMessage = ' ';
					this.showModalFin = true;
				}
			});
		},
		moveHome() {
			// &  
			this.userLogout(this.userInfo.userid);
			sessionStorage.removeItem('access-token'); //  
			sessionStorage.removeItem('refresh-token'); //  
			this.$router.push({ name: 'home' });
		},
	},
};
</script>
<style></style>
